import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { SalesChartTableReport } from 'src/app/sales/interface/vh-report';
import { LanguageService } from 'src/app/sales/services/language.service';
import ApexCharts, { ApexOptions } from 'apexcharts';
import { DatePipe } from '@angular/common';
import { FunctionService } from 'src/app/sales/services/function.service';
@Component({
  selector: 'app-sales-employee',
  templateUrl: './sales-employee.component.html',
  styleUrls: ['./sales-employee.component.scss']
})
export class SalesEmployeeComponent {
  public date: Date[] = [new Date(), new Date()];
  chart: ApexCharts;
  reportConfig: any = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_employee
  selectedValue: any = 'all';
  isChart: boolean = false;
  dataColor: Array<any> = ["#Ffda45", "#48c0e3", "#f9be57", "#ee3d85", "#f8756d", "#c667ca", "#d6df3a", "#06467b", "#3c86c7", "#1fd2cd", "#373737"];
  list_show: any = [];
  dataReport: SalesChartTableReport = this.initDataReport();
  showDataTable: any = { docs: [], sales_atax: 0, sales_btax: 0, quantity: 0 };
  list_employee: any = [
    { _id: 'all', name: this.languageService.translate('All') }
  ];
  list_search_employee : any= []
  stateObj: any = {
    startTime: '',
    endTime: '',
    selectedValue: ''
  }
  branchList: Array<any> = [];
  search_branchList: Array<any> = [];
  id_branch_report: string = ''
  useBranch: boolean = this.fncService.checkUsingBranch()
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private router: Router,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    private vhAuth: VhAuth,
    public languageService: LanguageService,
    private datePipe: DatePipe,
    private cdRef: ChangeDetectorRef,
    private fncService: FunctionService
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
    this.getListEmployee();
    this.getBranches()
    let data: any = this.router.getCurrentNavigation().extras.state

    if (data) {
      let stateSubObj = data.stateObj;
      this.dataReport = stateSubObj.dataReport;
      this.date = [this.dataReport.startTime, this.dataReport.endTime];
      this.selectedValue = stateSubObj.selectedValue;
      this.id_branch_report = stateSubObj.id_branch_report
    }
  }

  heightScroll: string;
  /**
   * xem báo cáo
   * @example this.showReport()
   */
  showReport() {
    this.dataReport = this.initDataReport(this.date[0], this.date[1]);
    if (this.isChart) this.chart.updateOptions(this.initOption([], []));
    if (this.selectedValue != 'all') {
      this.isChart = false;
    }
    this.getSales(this.selectedValue, this.date[0], this.date[1]);
  }
  ngAfterViewChecked() {
    if (document.querySelector("#sales-employee") && document.querySelector(".ant-table-thead") && document.querySelector(".sales-employee-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#sales-employee").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".sales-employee-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 65 + "px";
    }
    this.cdRef.detectChanges();
  }
  /**
   * get dữ liệu nv và xử lý _id để xem báo cáo
   * @example this.getListCustomer()
   */
  getListEmployee() {
    for (let item of this.vhAuth.getlocalEmployees()) {
      this.list_employee.push({ _id: item._id, name: item.name })
    }
    this.list_search_employee = this.list_employee
  }

  /**
   * lọc nv theo tên
   * @example this.searchEmployee('')
   */
  searchEmployee(value){
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.list_employee = this.vhAlgorithm.searchList(tempVal, this.list_search_employee, ['name','phone'])
    else this.list_employee = this.list_search_employee
  }

  initDataReport(startTime: Date = new Date(), endTime: Date = new Date()): SalesChartTableReport {
    startTime.setHours(0, 0, 0, 0)
    endTime.setHours(23, 59, 59, 59)
    return { startTime, endTime, dataChart: [], dataTable: { docs: [], sales_atax: 0, sales_btax: 0, quantity: 0 } }
  }

  /**
   * bắt sk thay đổi nv xem báo cáo
   * @example this.filterProducts('DLKNJWDJNIWDI')
   */
  filterProducts(optionValue) {
    this.selectedValue = optionValue;
    // this.dataReport = this.initDataReport(this.startTimeBackUp, this.endTimeBackUp);
    // this.getSales(this.selectedValue, this.startTimeBackUp, this.endTimeBackUp);
  }
  //** hàm sort cho các cột */
  statusQuantity = false
  statusSales = false
  statusbSales = false
  statusName = false
  /**
   * sort với trường là dạng số
   * @param sort 
   * @param value tên trường cần sort
   * @example this.sort(1, 'total')
   */
  sort(sort, value) {
    if (sort) {
      this.dataReport.dataTable.docs = this.vhAlgorithm.sortNumberbyASC([...this.dataReport.dataTable.docs], value)
    }
    else {
      this.dataReport.dataTable.docs = this.vhAlgorithm.sortNumberbyDESC([...this.dataReport.dataTable.docs], value)
    }
  }
  /**
   * sort với trường là dạng chuỗi
   * @param sort 
   * @param value tên trường cần sort
   * @example this.sort(1, 'name')
   */
  sortName(sort, value) {
    if (sort) {
      this.dataReport.dataTable.docs = this.vhAlgorithm.sortVietnamesebyASC([...this.dataReport.dataTable.docs], value)
    }
    else {
      this.dataReport.dataTable.docs = this.vhAlgorithm.sortVietnamesebyDESC([...this.dataReport.dataTable.docs], value)
    }
  }

  /**
   * bắt sk thay đổi thời gian xem báo cáo
   * @param event 
   * @example this.changeTime(['2023-06-27T01:21:41.397Z','2023-06-27T01:21:41.397Z'])
   */
  changeTime(event) {
    if (new Date(event[1].setHours(0,0,0,0)).getTime() - new Date(event[0].setHours(0,0,0,0)).getTime() <=  this.fncService.checkDateReport() * 86000000) {
      this.dataReport = this.initDataReport(event[0], event[1])
      if (this.isChart) this.chart.updateOptions(this.initOption([], []))
    } else {
      this.vhComponent.showToast(4000, this.languageService.translate("The time limit to view is") + " " + this.fncService.checkDateReport() + " " +  this.languageService.translate("days"), "alert-toast")
      this.date = [new Date(event[0]), new Date(event[0].getTime() + this.fncService.checkDateReport() * 86000000)]
      this.dataReport = this.initDataReport(new Date(this.date[0]), new Date(this.date[1]))
    }
  }

  /**
   * get dữ liệu xem báo cáo
   */
  getSales(id_product, startDate, endDate) {
    this.vhComponent.showLoadingNotDuration("", "transparent-loading").then(() => {
      this.vhQuerySales.getReportSales_Employees_byEmployees(startDate, endDate, id_product, this.id_branch_report)
        .then((res) => {
          if (res) {
            if (!res.empty) {
              //this.isChart = false
              let data = res.docs;
              // dữ liệu dạng chart (hiển thị dữ liệu sau thuế)
              for (let i = 0; i < 10; i++) {
                if (!data[i]) break
                else {
                  this.dataReport['dataChart'].push({
                    color: this.dataColor[i],
                    label: data[i]['name'],
                    value: data[i]['sales_atax'],
                  })
                }
              }
              this.dataReport['dataTable'] = { ...res }
              this.sort(this.statusQuantity, 'quantity')
            }
            if (this.isChart) this.updateChart(this.dataReport['dataChart'], true)
          }

        }).finally(() => this.vhComponent.hideLoading(0))
    })

  }
  /**
   * hiển thị chart ra giao diện
   * @example this.initChart()
   */
  initChart(data_chart?) {
    setTimeout(() => {
      this.chart = new ApexCharts(document.querySelector("#sales-employee-chart"), this.initOption([], []));
      this.chart.render();
      if (data_chart) this.updateChart(data_chart)
    }, 200)
  }
  /**
   * bắt sk thay đổi kiểu xem báo cáo (chart or table)
   */
  showChart() {
    this.isChart = !this.isChart;
    this.isChart ? this.initChart(this.dataReport['dataChart']) : this.chart.destroy()
  }
  /**
   * gán lại dữ liệu cho chart
   */
  updateChart(data_chart, initStatus: boolean = false) {
    this.chart.updateOptions(this.initOption(
      !data_chart.length || !data_chart[0].value ? [] : data_chart.map(item => item.value),
      !data_chart.length || !data_chart[0].value ? [] : data_chart.map(item => item.label),
      initStatus
    ))
  }

  initOption(series, labels, initStatus: boolean = false): ApexOptions {
    return {
      series: series,
      chart: {
        height: "auto",
        type: "pie"
      },
      dataLabels: {
        dropShadow: {
          enabled: false
        }
      },
      colors: this.dataColor,
      labels: labels,
      fill: {
        type: "solid",
        colors: this.dataColor,
      },
      legend: {
        show: false
      },
      yaxis: {
        labels: {
          formatter: (value) => {
            return this.vhAlgorithm.vhcurrencyunit(value);
          }
        }
      },
      states: {
        active: {
          filter: {
            type: "none"
          }
        },
        normal: {
          filter: {
            type: "none"
          }
        },
        hover: {
          filter: {
            type: "none"
          }
        }
      },
      noData: {
        text: (this.languageService.translate('No data!'))
      }
    }
  }

  /**
   * get chi nhánh được xem báo cáo của user
   */
  getBranches() {
    let branches = [...this.vhQuerySales.getlocalBranchs()] // danh sách toàn bộ chi nhánh
    let employee = this.vhAuth.getUser() // thông tn nhân viên
    let defaultBranch = this.vhQuerySales.getDefaultBranch()._id // chi nhánh mặc định khi đăng nhập (lấy id chi nhánh)

    if (this.useBranch) { // kiểm tra xem có 2 chi nhánh trở lên

      if (employee['owner'] == 'boss') { // kiểm tra tài khoản đăng nhập là boss
        this.branchList = branches // gán danh sách chi nhánh show ra HTML
        if (this.branchList.length > 1) this.branchList.unshift({ _id: "all", name: "All" })
        this.id_branch_report = defaultBranch // gán mặc định chi nhánh mặc định xem báo cáo là chi nhánh đăng nhập vô
      }

      else { // trường hợp tài khoản là nhân viên
        this.vhAuth.getPermission(employee['_id']).then(result => { // get quyền nhân viên được xem báo cáo chi nhánh nào
          branches.forEach(item => {
            if (result[`${item._id}_view_report_branch`]) this.branchList.push({ ...item }) // kiểm tra chi nhánh đc xem báo cáo, gán cho branchList show ra HMLT 
          })
          if (this.branchList.length > 1) { // trường hợp được xem báo cáo 2 chi nhánh trở lên
            this.branchList.unshift({ _id: "all", name: "All" }) // đẩy xem tất cả vô, nếu có 2 chi nhánh trở lên
            for (let i in this.branchList) {
              if (this.branchList[i]._id == defaultBranch) return this.id_branch_report = defaultBranch // nếu có gán chi nhánh xem báo mặc định là chi nhánh mặc định (đăng nhập)
              else this.id_branch_report = this.branchList[1]._id // ngược lại gán chi nhánh mặc định là chi nhánh thứ 1
            }
          }
          else if (this.branchList.length == 1) { // nếu chỉ đc xem báo cáo 1 chi nhánh , lấy mặc định là chi nhánh đó
            this.id_branch_report = this.branchList[0]._id
          }
          else this.vhComponent.alertMessageDesktop("warning", "Bạn không được chọn chi nhánh xem báo cáo") // kg có chi nhánh xem báo cáo
        })
        
      }
      this.search_branchList = this.branchList
    } else this.id_branch_report = defaultBranch // chỉ xài 1 chi nhánh 
  }
  /**
   * lọc chi nhánh theo tên
   * @param value 
   */
  search(value){
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.branchList = this.vhAlgorithm.searchList(tempVal, this.search_branchList, ['name'])
    else this.branchList = this.search_branchList
  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/reports']);
  }

  onCurrentPageDataChange(value) {
  }
  /**--------------------- */
  gotoDetailProducts(data) {
    let stateObj = { selectedValue: this.selectedValue, id_branch_report: this.id_branch_report, dataReport: this.dataReport }
    this.router.navigate(['/sales/dashboard/manage/reports/sales-employee/detail-employee'], {
      state: { employee: { ...data }, stateObj }
    })
  }
  /**
   * tải báo cáo dưới dạng hình ảnh
   */
  downloadImg() {
    this.vhComponent.downloadImg("chart-image",
      `${this.languageService.translate("_employee_sales_report")}_${this.languageService.translate("_from")}_${this.format_date(this.dataReport.startTime)}_${this.languageService.translate("_to")}_${this.format_date(this.dataReport.endTime)}_${this.id_branch_report == "all" ? "" : this.vhAlgorithm.changeAlias(this.vhQuerySales.getlocalBranch(this.id_branch_report).name).replace(/\s/g, "_")}`).then(() => { })
  }
  /**
   * 
   * @param date 
   * @returns string
   * @example let date = this.format_date('2023-06-27T01:10:34.396Z') => output '27_06_2023'
   */
  format_date(date) {
    return this.datePipe.transform(date, "dd_MM_yyyy")
  }

   /**
    * xuất dữ liệu sang file excel và tải về
    */
  export() {
    // xử lý tạo tên file excel
    let data = [];
    let name = `${this.languageService.translate("_employee_sales_report")}_${this.languageService.translate("_from")}_${this.format_date(this.dataReport.startTime)}_${this.languageService.translate("_to")}_${this.format_date(this.dataReport.endTime)}`;
    let total: any = new Object();
    // if (this.reportConfig.product_picture)
    //   total[this.languageService.translate('Image')] = "";
    total[this.languageService.translate('Employee')] = this.languageService.translate('Total');
    total[this.languageService.translate('Quantity')] = this.dataReport['dataTable'].quantity;
    if (this.reportConfig)
      total[this.languageService.translate('Sales before tax')] = this.dataReport['dataTable'].sales_btax;
    total[this.languageService.translate('Sales after tax')] = this.dataReport['dataTable'].sales_atax;
    data.push(total);

    for (let _row of this.dataReport['dataTable'].docs) {
      let item: any = new Object();
      // if (this.reportConfig.product_picture)
      //   item[this.languageService.translate('Image')] = _row.img;
      item[this.languageService.translate('Employee')] = _row.name;
      item[this.languageService.translate('Quantity')] = _row.quantity;
      if (this.reportConfig)
        item[this.languageService.translate('Sales before tax')] = _row.sales_btax;
      item[this.languageService.translate('Sales after tax')] = _row.sales_atax;
      data.push(item);
    }

    this.vhAlgorithm.exportXLSX(data, name).then(() => { }).catch(err => { })
  }
}
