import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SalesEmployeeComponent } from './sales-employee.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';

const routes: Routes = [
  {
    path: '',
    component: SalesEmployeeComponent
  },
  {
    path: 'detail-employee',
    loadChildren: () => import('./detail-employee/detail-employee.module').then(m => m.DetailEmployeeModule)
  }
]

@NgModule({
  declarations: [SalesEmployeeComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    IonicModule,
  ]
})
export class SalesEmployeeModule { }
